import axios from "axios";
import qs from 'qs'


let supertype_map = new Map()
supertype_map.set('product', 'Product')
supertype_map.set('entity', 'Entity')


export const doLogin = (userid, password) => {
    refreshToken(userid, password, () => {
        localStorage.setItem('userid', userid)
        localStorage.setItem('password', password)
        window.location.href = '/';
    })

}

const refreshToken = (userid, password, callbackmethod) => {
    const inputparms = {
        userId: userid,
        password: password
    }
    axios.post("https://" + localStorage.getItem('stepurl') + "/graphqlv2/auth", qs.stringify(inputparms), {
        headers: {
            "Content-Type": `application/x-www-form-urlencoded`,
            // "Origin": localStorage.getItem('stepurl'),
            // 'Access-Control-Request-Headers':'Authorization, Content-Type'
        }
    }).then(res => {
        console.log(res)
        if (res.status === 200) {
            localStorage.setItem("token", res.data);
        }
        callbackmethod();
    }).catch(err => {
        localStorage.clear();
        alert(err)
    });
}

export const getNodeInfo = (workspace, supertype, id, callbackmethod) => {

    const valuesquery = '    values{ ' +
        '      __typename ' +
        '      attribute{ ' +
        '        title ' +
        '        id ' +
        '        calculated ' +
        '      } ' +
        '      simpleValue ' +
        '    }     ';
    const referencesquery = '    referencesByReferenceType{ ' +
        '      referenceType{ ' +
        '        id ' +
        '      } ' +
        '      referenceEntries{ ' +
        valuesquery +
        '        target{ ' +
        '          ... on Entity{ ' +
        '            id ' +
        '            name ' +
        '          } ' +
        '         ... on Product{ ' +
        '            id ' +
        '          } ' +
        '        } ' +
        '      } ' +
        '    } ';
    const dataconatinerquery = '    dataContainers{ ' +
        '      dataContainerType{ ' +
        '        id ' +
        '        name ' +
        '        multivalued ' +
        '      } ' +
        '      dataContainers{ ' +
        '        id ' +
        valuesquery +
        '      } ' +
        '    } ';
    const getNodeInfoquery = 'query { ' +
        '  #SUPERTYPE(id:"#ID" context:"Context1" workspace:"#WORKSPACE"){ ' +
        '    id ' +
        '    name ' +
        '   currentRevisionLastEdited ' +
        '   approvalStatus ' +
        '    path { ' +
        '      id ' +
        '    } ' +
        '    objectType{ ' +
        '      id  ' +
        '      title ' +
        '    } ' +
        '    values{ ' +
        '      attribute{ ' +
        '        title ' +
        '        id ' +
        '        calculated ' +
        '      } ' +
        '      simpleValue ' +
        '    }     ' +
        '    children{ ' +
        '      pageElements{ ' +
        '        id ' +
        '        name ' +
        '      } ' +
        '    } ' +
        referencesquery +
        dataconatinerquery +
        '  } ' +
        '} ';
    let query = getNodeInfoquery;
    query = query.replace('#ID', id).replace('#WORKSPACE', workspace).replace('#SUPERTYPE', supertype);
    const jsonobj = { query: query };

    dofetch(jsonobj, callbackmethod)
}

export const setNodeValue = (workspace, supertype, id, attid, value, callbackmethod) => {
    let query = 'mutation { ' +
        '  setNodeValue(context: "Context1", workspace: "#WORKSPACE", input: {id: "#ID", type: #SUPERTYPE, attribute: "#ATTID", value: {simpleValue: "#VALUE"}}) { ' +
        '    success ' +
        '    errors { ' +
        '      message ' +
        '    } ' +
        '  } ' +
        '} ';
    query = query.replace('#ID', id).replace('#SUPERTYPE', supertype_map.get(supertype)).replace('#ATTID', attid).replace('#VALUE', value).replace('#WORKSPACE', workspace);
    const jsonobj = { query: query };

    dofetch(jsonobj, callbackmethod)

}

export const setNodeName = (workspace, supertype, id, value,callbackmethod) => {
    let query = 'mutation { ' +
                '  setNodeName(context:"Context1" workspace:"#WORKSPACE" input:{id:"#ID" type:#SUPERTYPE name:"#VALUE"}){ ' +
                '    success ' +
                '    errors{ ' +
                '      message ' +
                '    } ' +
                '  } ' +
                '} ';
    query = query.replace('#WORKSPACE', workspace).replace('#ID', id).replace('#SUPERTYPE', supertype_map.get(supertype)).replace('#VALUE', value);
    const jsonobj = { query: query }
    dofetch(jsonobj, callbackmethod)
}

export const setDataContainerValue = (workspace, id, attid, value, callbackmethod) => {
    let query = 'mutation { ' +
        '  setDataContainerValue(context:"Context1" workspace:"#WORKSPACE" input:{ ' +
        '    id:"#ID" ' +
        '    attribute:"#ATTID"  ' +
        '    value:{simpleValue:"#VALUE"}}){ ' +
        '    success ' +
        '    errors{ ' +
        '      message ' +
        '    } ' +
        '  } ' +
        '} ';
    query = query.replace('#WORKSPACE', workspace).replace('#ID', id).replace('#ATTID', attid).replace('#VALUE', value);
    const jsonobj = { query: query }
    dofetch(jsonobj, callbackmethod)
}

const dofetch = (jsonobj, callbackmethod) => {

    refreshToken(localStorage.getItem('userid'), localStorage.getItem('password'), () => {

        const authkey = 'Bearer ' + localStorage.getItem('token');
        fetch('https://' + localStorage.getItem('stepurl') + '/graphqlv2/graphql', {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Authorization": authkey,
                "Origin": localStorage.getItem('stepurl'),
                'Access-Control-Request-Headers': 'Authorization, Content-Type'
            },
            body: JSON.stringify(jsonobj),
        }).then(res => {
            console.log(res.status)
            if (res.status === 200) {
                console.log('success')
            } else {
                console.log('error')
            }
            return res.json()
        }).then(data => {
            console.log(data)
            callbackmethod(data)

        })
    })

}