import TreeElement from "./TreeElement";
import '../css/treepage.css'
import { useEffect, useState } from "react";
import { getNodeInfo, setDataContainerValue, setNodeName, setNodeValue } from "../util/DataUtil";
import { Loadingarea } from "../util/Commoncomp"


const Treepage = ({ supertype, id, name, workspace }) => {

    const [condid, setCondid] = useState("");
    const [isLoading, setIsLoading] = useState("");

    const [result_id, setResultid] = useState("")
    const [result_name, setResultname] = useState("")
    const [result_lastedited, setResultLastEdited] = useState("")
    const [result_appstatus, setResultAppStatus] = useState("")
    const [result_values, setResultValues] = useState([])
    const [result_path, setResultpath] = useState([])
    const [result_dcs, setResultDcs] = useState([])
    const [result_refs, setResultRefs] = useState([])

    const [curretntab, setCurrentTab] = useState(supertype)



    useEffect(() => {
        if (condid !== "" && condid !== undefined) {

            handleSearchbutton();
        }
    }, [condid, workspace])


    const getCurretnCondid = () => {
        return condid;
    }
    const handleSearchbutton = () => {
        setIsLoading(true)
        getNodeInfo(workspace, supertype, condid, (data) => {
            if (data.data[supertype] !== null) {
                setResultid(data.data[supertype].id)
                setResultname(data.data[supertype].name)
                setResultLastEdited(data.data[supertype].currentRevisionLastEdited);
                setResultAppStatus(data.data[supertype].approvalStatus);
                setResultValues(data.data[supertype].values)
                setResultpath(data.data[supertype].path)
                setResultDcs(data.data[supertype].dataContainers)
                setResultRefs(data.data[supertype].referencesByReferenceType)
            } else {
                setResultid("")
                setResultname("")
                setResultAppStatus();
                setResultLastEdited();
                setResultValues([])
                setResultpath([])
                setResultDcs({})
                setResultRefs([])
            }

            setIsLoading(false)
        });
    }



    const changeTab = (tabid) => {
        setCurrentTab(tabid);
    }

    const GeneralTab = ({ result_id, result_name, result_values, result_lastedited, result_appstatus }) => {
        const [generaltab_name, setGeneralName] = useState(result_name)
        const [generaltab_values, setGeneralValues] = useState([...result_values])

        const onChangeResultName = (e) =>{
            const value = e.target.value;
            setGeneralName(value);
        }

        const setResultName = (e) =>{
            const value = e.target.value;
            setNodeName(workspace, supertype,result_id, value, ()=>{
                console.log('이름변경완료')
            })
        }


        const onChangeResultValue = (e) => {
            const attid = e.target.id;
            const value = e.target.value;
            const params = attid.split('^')

            let temp_values = [...generaltab_values]
            temp_values[params[0]].simpleValue = value;
            setGeneralValues(temp_values)
        }


        const setResultValue = (e) => {
            const attid = e.target.id;
            const value = e.target.value;
            const params = attid.split('^')

            
            setNodeValue(workspace, supertype, result_id, params[1], value, (data) => {
                if (data.data.setNodeValue.success === true) {
                    console.log('변경완료')
                }
            })
        }

        

        return (
            <>
                {result_id !== "" ?
                    <table className='table' style={{ width: '100%' }}>
                        <thead>
                            <tr>
                                <th colSpan={20}>
                                    General Info
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                result_id !== "" ?
                                    <>
                                        <tr>
                                            <td width='20%'><b>ID</b></td>
                                            <td>{result_id}</td>
                                        </tr>
                                        <tr>
                                            <td><b>NAME</b></td>
                                            <td>
                                                <input id="general_name" value={generaltab_name} onChange={(e) => { onChangeResultName(e) }}
                                                            onBlur={(e) => { setResultName(e) }}></input>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td><b>LastEdited</b></td>
                                            <td>{result_lastedited}</td>
                                        </tr>
                                        <tr>
                                            <td><b>ApprovalStatus</b></td>
                                            <td>{result_appstatus}</td>
                                        </tr>
                                        {generaltab_values.map(
                                            (value, index) => (
                                                <tr key={value.attribute.id}>
                                                    <td><b>{value.attribute.title}</b></td>
                                                    <td>
                                                        <input disabled={value.attribute.calculated || (workspace === 'Approved') ? true : false}
                                                            id={index + "^" + value.attribute.id} value={value.simpleValue} onChange={(e) => { onChangeResultValue(e) }}
                                                            onBlur={(e) => { setResultValue(e) }}></input>
                                                    </td>
                                                </tr>
                                            )
                                        )
                                        }
                                    </>
                                    : <tr>
                                        <td>Not found Data.</td>
                                    </tr>
                            }
                        </tbody>
                    </table>
                    : ""
                }
            </>
        )



    }

    const DataContainerTab = ({ result_dcs }) => {
        // const [dctab_dcs, setDcDcs] = useState([...result_dcs])
        return (
            <>
                {
                    result_dcs.length > 0 ?
                        result_dcs.map((dc, dc_index) => {
                            return (
                                <table className='table' style={{ width: '100%' }}>
                                    <thead>
                                        <tr>
                                            <th colSpan={200}>
                                                {dc.dataContainerType.id}-{dc.dataContainerType.name}
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {dc.dataContainerType.multivalued === false || dc.dataContainerType.multivalued === 'false' ?
                                            <DataContainerForm1 key={dc.dataContainerType.id} dataContainers={dc.dataContainers} />
                                            :
                                            <DataContainerForm2 key={dc.dataContainerType.id} dataContainers={dc.dataContainers} />
                                        }
                                    </tbody>
                                </table>
                            )
                        })
                        : <div>Not found Data.</div>
                }
            </>
        );
    }






    const DataContainerForm1 = ({ dataContainers }) => {
        //헤더가 없는 form
        const [dcform_dcs, setDcformDcs] = useState([...dataContainers]);

        const onChangeResultDC = (e) => {
            const attid = e.target.id;
            const value = e.target.value;
            const params = attid.split('^');

            let temp_dcs = [...dcform_dcs]
            temp_dcs[params[0]].values[params[1]].simpleValue = value;
            setDcformDcs(temp_dcs)
        }

        const setResultDCValue = (e) => {
            const attid = e.target.id;
            const value = e.target.value;
            const params = attid.split('^');

            setDataContainerValue(workspace, params[2], params[3], value, (data) => {
                if (data.data.setDataContainerValue.success === true) {
                    console.log('변경완료')
                }
            })
        }

        return (
            <>
                {
                    dcform_dcs.length > 0 ?
                        <>
                            {dcform_dcs.map((dcs, dcs_index) => {
                                return (
                                    <>
                                        <tr>
                                            <td width='20%'>ID</td>
                                            <td>{dcs.id}</td>
                                        </tr>
                                        {dcs.values.map((value, value_index) => {
                                            return (
                                                <tr>
                                                    <td>{value.attribute.id}</td>
                                                    <td><input disabled={value.attribute.calculated || (workspace === 'Approved') ? true : false}
                                                        id={dcs_index + "^" + value_index + "^" + dcs.id + "^" + value.attribute.id} value={value.simpleValue}
                                                        onChange={(e) => { onChangeResultDC(e) }} onBlur={(e) => { setResultDCValue(e) }}></input></td>
                                                </tr>
                                            )
                                        })}
                                    </>
                                )
                            })}
                        </>
                        :
                        <tr>
                            <td>Not found data.</td>
                        </tr>
                }
            </>
        )
    }

    const DataContainerForm2 = ({ dataContainers }) => {
        //헤더가 있는 form
        const [dcform_dcs, setDcformDcs] = useState([...dataContainers]);

        const onChangeResultDC = (e) => {
            const attid = e.target.id;
            const value = e.target.value;
            const params = attid.split('^');

            let temp_dcs = [...dcform_dcs]
            temp_dcs[params[0]].values[params[1]].simpleValue = value;
            setDcformDcs(temp_dcs)
        }

        const setResultDCValue = (e) => {
            const attid = e.target.id;
            const value = e.target.value;
            const params = attid.split('^');

            setDataContainerValue(workspace, params[2], params[3], value, (data) => {
                if (data.data.setDataContainerValue.success === true) {
                    console.log('변경완료')
                }
            })
        }
        return (
            <>
                {
                    dcform_dcs.length > 0 ?
                        dcform_dcs.map((dcs, dcs_index) => {
                            return (
                                <>
                                    {dcs_index === 0 ?
                                        <tr style={{ color: '#8442b7', textAlign: 'center' }}>
                                            <td>ID</td>
                                            {dcs.values.map(value => {
                                                return (
                                                    <td>
                                                        {value.attribute.id}
                                                    </td>
                                                )
                                            })}
                                        </tr>
                                        : ""
                                    }
                                    <tr>
                                        <td width='7%'>{dcs.id}</td>
                                        {
                                            dcs.values.map((value, value_index) => {
                                                return (
                                                    <td><input disabled={value.attribute.calculated || (workspace === 'Approved') ? true : false}
                                                        id={dcs_index + "^" + value_index + "^" + dcs.id + "^" + value.attribute.id} value={value.simpleValue}
                                                        onChange={(e) => { onChangeResultDC(e) }} onBlur={(e) => { setResultDCValue(e) }}></input></td>
                                                )

                                            })
                                        }
                                    </tr>
                                </>

                            )

                        }) :
                        <tr>
                            <td>Not found data.</td>
                        </tr>
                }
            </>
        )
    }

    const ReferenceTab = ({ result_refs }) => {
        const [reftab_refs, setReTabRefs] = useState([...result_refs]);


        return (
            <>
                {reftab_refs.map(ref => {
                    return (
                        <table className="table" style={{ width: '100%' }}>
                            <thead>
                                <tr>
                                    <th colSpan={200}>{ref.referenceType.id}</th>
                                </tr>
                            </thead>
                            <tbody>
                                {ref.referenceEntries.length > 0 ?
                                    <>
                                        {ref.referenceEntries.map((entry, index) => {
                                            return (
                                                <>
                                                    {index === 0 ?
                                                        <tr style={{ color: '#8442b7', textAlign: 'center' }}>
                                                            <td>Target ID</td>
                                                            <td>Target NAME</td>
                                                            {entry.values.map(value => {
                                                                return (
                                                                    <td>{value.attribute.id}</td>
                                                                )
                                                            })}
                                                        </tr>
                                                        : ""}
                                                    <tr>
                                                        <td>{entry.target.id}</td>
                                                        <td>{entry.target.name}</td>
                                                        {entry.values.map(value => {
                                                            return (
                                                                <td>{value.simpleValue}</td>
                                                            )
                                                        })}
                                                    </tr>
                                                </>

                                            )

                                        })}
                                    </>
                                    :
                                    <tr>
                                        <td>Not found data.</td>
                                    </tr>
                                }


                            </tbody>
                        </table>
                    )
                })

                }
            </>
        )
    }

    return (
        <>
            <Loadingarea isLoading={isLoading} />
            <div className="treemain">
                <div className="treearea" >
                    <table>
                        <thead>
                            <tr>
                                <th colSpan={10} style={{ textAlign: 'left' }}>
                                    <h2 style={{ textTransform: 'uppercase', color: '#8442b7' }}>{supertype} hierarchy</h2>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <TreeElement key={id} supertype={supertype} id={id} name={name} lvl="0" setCondid={setCondid}
                                getCurretnCondid={getCurretnCondid} setIsLoading={setIsLoading} workspace={workspace}
                                result_path={result_path} />
                        </tbody>
                    </table>
                </div>

                <div className="nodedetail">
                    <div className='searcondition_area'>
                        <div style={{ width: '25%', height: 'px', display: 'flex', alignItems: 'center', textTransform: 'uppercase', marginLeft: '10px' }}>
                            <h3>{supertype} ID-{condid}</h3>
                        </div>
                        <div className="refresh-icon" onClick={handleSearchbutton}></div>
                    </div>
                    <div className="tabmenu">
                        <div className={curretntab === supertype ? "tab-selected" : ""} onClick={() => { changeTab(supertype) }}>{supertype}</div>
                        <div className={curretntab === 'DC' ? "tab-selected" : ""} onClick={() => { changeTab('DC') }}>Data Container</div>
                        <div className={curretntab === 'REF' ? "tab-selected" : ""} onClick={() => { changeTab('REF') }}>Reference</div>
                    </div>
                    {curretntab === supertype ?

                        <GeneralTab result_id={result_id} result_name={result_name} result_values={result_values}
                            result_lastedited={result_lastedited} result_appstatus={result_appstatus} />
                        : ""
                    }
                    {curretntab === 'DC' ?
                        <DataContainerTab result_dcs={result_dcs} />
                        : ""
                    }
                    {curretntab === 'REF' ?
                        <ReferenceTab result_refs={result_refs} />
                        : ""
                    }

                </div>

            </div >
        </>


    )
}

export default Treepage;