import React, { useState } from 'react'
import Product from './Product'
import Entity from './Entity'
import { Link, Route, Routes } from 'react-router-dom'
import Treepage from './Treepage'


export default function Header() {

    const [currentmenu, setCurrentMenu] = useState('producttree');
    const [workspace, setWorkspace] = useState('Main')


    function handleLogout() {
        window.localStorage.clear();
        window.location.reload()
    }

    const changeWorspace = () => {
        if (workspace === 'Main') {
            setWorkspace('Approved')
        } else {
            setWorkspace('Main')
        }
    }



    return (
        <div style={{ width: '86%', display: 'block' }}>
            <div className='header'>
                
                <h1 style={{ width: '30%', display: 'flex', justifyContent: 'start', alignItems: 'end' }}><div className='step-logo' />STEP GraphQL UI</h1>
                <div style={{ width: '50%', display: 'flex', justifyContent: 'start', alignItems: 'end', height: '40px',fontSize:'20px' }}>{localStorage.getItem('stepurl')}</div>
                <div style={{ display: 'flex', alignItems: 'center', width: '22%', justifyContent: 'right'}}>
                    <button className='btn-default' onClick={handleLogout}>Logout</button>
                </div>
            </div>
            <div style={{ display: 'flex', justifyContent: 'right', marginTop: '5px' }}>
                <div className='select_ws' id={workspace} onClick={changeWorspace}>
                    <div>Main</div>
                    <div>Approved</div>
                </div>
            </div>

            <div className='headermenu'>
                <Link to="/" className={currentmenu === 'producttree' ? "menubutton menubutton-selected" : "menubutton"} onClick={() => { setCurrentMenu('producttree') }}>Product Tree</Link>
                <Link to="/entitytree" className={currentmenu === 'entitytree' ? "menubutton menubutton-selected" : "menubutton"} onClick={() => { setCurrentMenu('entitytree') }}>Entity Tree</Link>

            </div>

            <div className='mainarea'>
                <Routes>
                    <Route path="/" element={<Treepage key="producttree_page" supertype="product" id="Product hierarchy root"
                        name="Product hierarchy root" workspace={workspace} />} />
                    <Route path="/entitytree" element={<Treepage key="entitytree_page" supertype="entity" id="100305"
                        name="Entity root" workspace={workspace} />} />
                    <Route path="/product" element={<Product />} />
                    <Route path="/entity" element={<Entity />} />
                </Routes>
            </div>

        </div>
    )

}
