import React, {useRef, useState} from 'react'
import axios from 'axios';
import qs from 'qs'
import { doLogin } from '../util/DataUtil';


export default function Login() {
    
    const [userid, setUserid] = useState()
    const [password, setPassword] = useState()
    const [site, setSite] = useState("");
    

    function handleLoginClick(){
        if(!checkValid()){
            alert('접속할 사이트를 선택해주세요')
        }else{
            localStorage.setItem('stepurl',site)
            console.log(localStorage.getItem('stepurl',site))
            doLogin(userid,password)
        }
        
    }

    const checkValid = () =>{
        if(site === ""){
            return false;
        }
        return true;
    }

    function handleEnter(){
        if(window.event.keyCode === 13){
            handleLoginClick();
        }
    }

    const changeSite = (e) =>{
        setSite(e.target.value)
        localStorage.setItem('stepurl', e.target.value)
    }

    return (
    <div align="center" style={{marginTop:"10%",backgroundColor:'rgb(246 243 247)', color:'#bc48ff' ,width:'30%', paddingLeft:'20px' ,paddingRight:'20px'}}>
        <div className='alt'>
            <h1>STEP LOGIN</h1>
        </div>
        <div style={{float: 'top'}}>
            <div>
                <label htmlFor="userid" className="inp">
                    <input type="text" id="userid" placeholder="&nbsp;" value={userid} onChange={(e)=>{setUserid(e.target.value)}} style={{width:'92%'}}/>
                    <span className="label">UserId</span>
                    <span className="focus-bg"></span>
                </label>
            </div>
            <div>
                <label htmlFor="password" className="inp">
                    <input type="password" id="password" placeholder="&nbsp;" value={password} onChange={(e)=>{setPassword(e.target.value)}} style={{width:'92%'}}/>
                    <span className="label">PassWord</span>
                    <span className="focus-bg"></span>
                </label>
            </div>
            <div style={{marginTop:'26px'}}>
                <select onChange={changeSite} className='dropdown' value={site}>
                    <option value={""}>--선택--</option>
                    <option value={"apacpresales-sec-cmdm.mdm.stibosystems.com"}>SEC Poc Site</option>
                    <option value={"apacpresales-hmckia-step.mdm.stibosystems.com"}>HMC-KIA Poc Site</option>
                </select>
            </div>
            <div><button className='btn-primary'  style={{width:'100%'}}  type='button' onClick={handleLoginClick}>Login</button></div>
            
        </div>
        
    </div>
    
    )
}
