import React, { useEffect, useState } from "react";
import { getNodeInfo } from "../util/DataUtil";

const TreeElement = ({ supertype, id, name, lvl, setCondid, getCurretnCondid, setIsLoading, workspace, result_path }) => {
    const [chdidrenData, setChildrenData] = useState([])
    const [showChild, setShowChild] = useState('N');
    const localId_showchild = workspace + "_" + id + "_showchild";
    const localId_data = workspace + "_" + id;

    const localId_Main_showchild = "Main_" + id + "_showchild";
    const localId_Approved_showchild = "Approved_" + id + "_showchild";

    useEffect(() => {

        // setShowChild(localStorage.getItem(localId_showchild) === null ? 'N' :
        //     localStorage.getItem(localId_showchild));
        const temp_data = localStorage.getItem(localId_data) !== "" ? JSON.parse(localStorage.getItem(localId_data)) : null;


        if (temp_data !== null && temp_data[supertype] !== undefined &&
            localStorage.getItem(localId_showchild) === 'Y') {
            setShowChild('Y')
            setChildrenData(temp_data[supertype].children.pageElements)
        } else {

            if (localStorage.getItem(localId_showchild) === 'Y') {
                setIsLoading(true)
                getNodeInfo(workspace, supertype, id, (data) => {
                    localStorage.setItem(localId_data, JSON.stringify(data.data))
                    if (data.data[supertype] !== undefined) {
                        setChildrenData(data.data[supertype].children.pageElements)
                    }
                    setIsLoading(false)
                });
            } else {
                setChildrenData([])
            }

        }
    }, [showChild, workspace])

    useEffect(() => {
        if (localStorage.getItem(localId_Main_showchild) === 'Y' || localStorage.getItem(localId_Approved_showchild) === 'Y') {
            setIsLoading(true);
            setShowChild('Y');
            getNodeInfo(workspace, supertype, id, (data) => {
                localStorage.setItem(localId_data, JSON.stringify(data.data))
                if (data.data[supertype] !== undefined && data.data[supertype] !== null) {
                    setChildrenData(data.data[supertype].children.pageElements)
                } else {
                    setChildrenData([])
                }
                setIsLoading(false)
            });
        }

    }, [workspace])


    const searchChildren = () => {
        console.log(showChild)
        if (showChild === 'Y') {
            localStorage.setItem(localId_showchild, 'N')
            localStorage.removeItem(localId_data)
            setShowChild('N')
        } else {
            localStorage.setItem(localId_showchild, 'Y')
            setShowChild('Y')
        }

    }

    const showDetail = (e) => {
        setCondid(e.target.id)
    }

    const marginleft = (Number(lvl) * 50) + "px";
    return (

        <>
            {id !== undefined ?
                <>
                    <tr style={{ textAlign: 'start' }}>
                        <td>
                            <div className="treeele" style={{ marginLeft: marginleft }}>
                                <b style={{ cursor: 'pointer' }} onClick={searchChildren} >{showChild === 'Y' ? " - " : " + "}</b>
                                <button id={id} className="btn-small" onClick={showDetail}>{id} - {name}</button>
                                {getCurretnCondid() === id ?
                                    <button id={id} className="btn-selected" onClick={showDetail}></button>
                                    : ""
                                }
                            </div>

                        </td>
                    </tr>
                    {showChild ? chdidrenData.map(child => (<TreeElement key={child.id} supertype={supertype} id={child.id} name={child.name}
                        lvl={Number(lvl) + 1} setCondid={setCondid} getCurretnCondid={getCurretnCondid} setIsLoading={setIsLoading} workspace={workspace}
                        result_path={result_path} />)) : ""}
                </>
                :
                <tr>
                    <td>
                        Not Found Data.
                    </td>
                </tr>
            }
        </>

    );

}

export default TreeElement;