import React,{useEffect, useRef, useState} from 'react';
import {  gql,useQuery } from '@apollo/client';
import { getEntityInfo, getNodeInfo, setNodeValue } from '../util/DataUtil'


export default function Entity() {
  const queryParam = new URLSearchParams(window.location.search)
  const [condEntityid, setCondEntityid] = useState(queryParam.get('id'));
  console.log(queryParam.get('id'))

  const [result_id, setResultid] = useState()
  const [result_name, setResultname] = useState()  
  const [result_values, setResultValues] = useState([])

  useEffect(()=>{
      console.log("entity effect")
      if(condEntityid !== ""){
      
        handleSearchbutton();
      }
      
  },[condEntityid])
 

  const handleSearchbutton =() =>{
    console.log(condEntityid + "---조회")
      getNodeInfo("entity",condEntityid, (data)=>{
        setResultid(data.data.entity.id)
        setResultname(data.data.entity.name)
        setResultValues(data.data.entity.values)
      });
  }

  const onChanageValue = (e) =>{

    const attid = e.target.id;
    const value = e.target.value;
    let temp_values = [...result_values]
    let temp_index = -1;   

    temp_values.filter((value,index) =>{ 
      if(value.attribute.id === attid){
        console.log()
        temp_index = index;
        return true;
      } 
    });
    temp_values[temp_index].simpleValue = value;
    setResultValues(temp_values)
    
    
  } 

  const setStepValue = (e) =>{
    const attid = e.target.id;
    const value = e.target.value;
    setNodeValue("Entity", result_id,attid,value, (data)=>{
      if(data.data.setNodeValue.success === true){
        console.log('변경완료')
      }
    })
  }



      
  return (
    <>
    <div className='searcondition_area'>
      <label htmlFor="entityid" className="inp">
          <input type="text" id="entityid" placeholder="&nbsp;" value={condEntityid} onChange={(e)=>{setCondEntityid(e.target.value)}} style={{width:'30%'}}/>
          <span className="label">Entity ID</span>
          <span className="focus-bg"></span>
      </label>
      <button className='btn-small' onClick={handleSearchbutton}>Search</button>
    </div>
    <table className='table' style={{width: '100%'}}>
      <thead>
        <tr>
          <th colSpan={2}>entity Search Result </th>
        </tr>
      </thead>
      <tbody>
        {
          result_id !== ""?
          <>
            <tr>
              <td width='20%'><b>ID</b></td>
              <td>{result_id}</td>
            </tr>
            <tr>
              <td><b>NAME</b></td>
              <td>{result_name}</td>
            </tr>
            {result_values.map(
                (value) => (
                    <tr key={value.attribute.id}>
                      <td><b>{value.attribute.title}</b></td>
                      <td>
                        <input disabled={value.attribute.calculated?true:false} id={value.attribute.id} value={value.simpleValue} onChange={(e)=>{onChanageValue(e)}}  onBlur={(e)=>{setStepValue(e)}}></input>
                      </td>
                    </tr>
                  )
              )
            }
          </>
          :<tr>
              <td>Not found Data.</td>
            </tr> 
        }  
      </tbody>  
    </table>
    </>
    
  );
}
